<template>
    <section class="section section-shaped section-lg my-0">
        <div class="shape shape-style-1 bg-gradient-default">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
        </div>
        <div class="container pt-lg-md text-white">
            <div class="row justify-content-center">
<p>
    Rejoins une communauté de plus d'une centaine d'Achiever qui te conseillerons sur leurs succès et leurs échecs via leur ToDoList
</p>

                
                
            </div>
                <div class="row justify-content-center"><h1 id="fontLucky" class=" text-white " >Mode d'emploi pour {{tabCard.title}}</h1></div>
                <div class="row">
                    <div class="col-6">
                        <h2 class="text-white">{{tabCard.sector}}</h2>
                    </div>
                     <div class="col-6">
                        <p class="text-white">Taux de réussite ressenti {{tabCard.rateSucceed}} %</p>
                    </div>
                </div>
                <div class="row">
                   <p class="text-white">Description: {{tabCard.description}}</p>
                </div>
                
                                    <div class="row" v-for="(i,index) in tabTasks" :key="index">
                <!--<base-alert type="success" icon="ni ni-like-2" dismissible>
                <span slot="text"><strong>Success!</strong> Bien ajouté</span>
            </base-alert>-->
            <div  class="col-10 bg-success m-2" v-if="i.isToDo==true">
            
<div class="row" v-if="boolDisabledOption==false">
    <div class="col-sm-8 col-md-8">
                        <p class="text-white">{{i.description}}</p>
                
    </div>
        <div class="col-lg-2 col-md-8">
                           <p>Ai-je réalisé cette tâche?</p> <input  type="checkbox" disabled >
    </div>
        <div class="col-lg-2 col-md-8">
                            <p>Est-ce pertinent?</p><input  type="checkbox" disabled>
    </div>
</div>
<div class="row" v-else>
    <div class="col-sm-8 col-md-8">
                        <p class="text-white">{{i.description}}</p>
                
    </div>
        <div class="col-lg-2 col-md-8">
                           <p>Ai-je réalisé cette tâche?</p> <input  type="checkbox" disabled >
    </div>
        <div class="col-lg-2 col-md-8">
                            <p>Est-ce pertinent?</p><input  type="checkbox" disabled>
    </div>

</div>



            </div>

                        <div  class="col-10 bg-danger m-2" v-if="i.isToDo==false">
            
<div class="row">
    <div class="col-8">
                        <p class="text-white">{{i.description}}</p>
                
    </div>
    <div class="col-lg-2 col-md-8">
                           <p>Ai-je réalisé cette tâche?</p> <input  type="checkbox" disabled >
    </div>
        <div class="col-lg-2 col-md-8">
                            <p>Est-ce pertinent?</p><input  type="checkbox" disabled>
    </div>

</div>
            </div>
                </div>
              
                <div class="row">
                    <p>Vous souhaitez utiliser cette ToDoList pour votre avancée? Cliquez sur Rejoindre. Si vous n'êtes pas encore <strong>inscrit</strong> cliquez sur rejoindre et inscrivez vous gratuitement!</p>
                    <button v-if="!(boolDisabledOption)" class="btn btn-light btn-sm m-2" @click.prevent="rejoindre">Rejoindre</button>
                </div>

        </div>
    </section>

</template>
<script>
import Modal from "@/components/Modal.vue";
import BaseSlider from "../components/BaseSlider.vue";
import BaseDropdown from "../components/BaseDropdown.vue";
import BaseInput from "../components/BaseInput.vue";
import Tabs from "@/components/Tabs/Tabs.vue";
import TabPane from "@/components/Tabs/TabPane.vue";
import BaseNav from "@/components/BaseNav";
import CardPerso from "./components/JavascriptComponents/CardPerso.vue";
import BaseCheckbox from "../components/BaseCheckbox.vue";

export default {
     components: {
    Modal,BaseSlider,BaseDropdown,BaseInput,Tabs,TabPane,BaseNav,CardPerso,BaseCheckbox
  },
 
  data() {
    return {
        tabCard:{},
        tabTasks:[],
      boolDisabledOption:false,
      
    };
  },
  methods: {
    rejoindre(){
      if(localStorage.leTokenUserAchieve){
        const x=location.href;
        this.$router.push({ path: 'seeCard', query: { id: x.split('seeCardNtw?id=')[1] } });
      }
      else{
        this.$router.push('register');
      }
    }

      
  },
    created() {
        let idUrl=this.$route.query.id
    fetch("https://achieve-back.herokuapp.com/card/"+idUrl, {
      method: "GET",
      headers: {
        'Accept': "application/json",
        "Content-Type": "application/json",
       
      },
    })
      .then((result) => {
       
        return result.json();
      })
      .then((tableauCard) => {
        this.tabCard = tableauCard;
      return     fetch("https://achieve-back.herokuapp.com/card/"+idUrl+"/allTasks", {
      method: "GET",
      headers: {
        'Accept': "application/json",
        "Content-Type": "application/json",
       
      },
    });})
    .then((result)=>{
return result.json();
    })
    .then((tabTasks)=>{
this.tabTasks=tabTasks;
    });
  }, 
};
</script>
<style >
#fontLucky{
    font-family: 'Luckiest Guy', cursive;
}
</style>
