<template>
    <section class="section section-shaped section-lg my-0">
        <div class="shape shape-style-1 bg-gradient-default">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
        </div>
        <div class="container pt-lg-md">
            <div class="row justify-content-center">
                <div class="col-lg-5">
                    <card type="secondary" shadow
                          header-classes="bg-white pb-5"
                          body-classes="px-lg-5 py-lg-5"
                          class="border-0">

                        <template>
                            <div class="text-center text-muted mb-4">
                                <small>Connectez-vous</small>
                            </div>
                            <form role="form">
                                <base-input alternative
                                            class="mb-3"
                                            placeholder="Email"
                                            addon-left-icon="ni ni-email-83"
                                            v-model="email"
                                            required>
                                </base-input>
                                <base-input alternative
                                            type="password"
                                            placeholder="Password"
                                            addon-left-icon="ni ni-lock-circle-open"
                                            v-model="password"
                                            required>
                                </base-input>

                                <div class="text-center">
                                    <base-button type="primary" class="my-4" v-on:click.prevent="verifyUserLogin">Se connecter</base-button>
                                </div>
                            </form>
                        </template>
                    </card>
                </div>
            </div>
        </div>
    </section>
</template>
<script>

export default {

  data() {
    return {
      email: "",
      password: "",
      messageErr: "",
    };
  },
  methods: {
    //verification de l'utilisateur lors du login
    verifyUserLogin() {
      //envoie de l'authentification au back
      fetch("https://achieve-back.herokuapp.com/user/login", {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          email: this.email,
         password: this.password,
        }),
      })
        .then((data) => {
          if (data.ok) {
            return data.json();
          }
          throw new Error("Something went wrong");
        })
        .then((data) => {
          //enregistrement du token dans le local storage pour avoir les informations de bases sur l'utilisateur quelquesoit sa localisation sur la page
          //si probleme, un message s'affichera
          localStorage.setItem("leTokenUserAchieve", JSON.stringify(data));
          
          this.$router.push("room");
        })
        .catch(() => {
          /*return (document.getElementById("messageErr").textContent =
            "Les identifiants tapés n' ont pas été retrouvé");*/
        });
    },
  },
  created() {
    localStorage.setItem("AccessLeTokenUserAchieve", JSON.stringify({boolAppearRoom:false}));
  },
};
</script>
