<script>
import Modal from "@/components/Modal.vue";
export default {
      components: {
    Modal
  },
    data(){
        return{
            pseudo:"",
            socialNetwork:"",
            password:"",
            email:"",
                  modals: {
        modal1: false,
        modal2: false,
        modal3: false
      }
