                                    <li class="nav-item">
                                        <a class="nav-link nav-link-icon" @click="likerMessage(uneCard._id, 1)">
                                            <i class="ni ni-favourite-28"></i>
                                            <span class="nav-link-inner--text d-lg-none">Liker</span>
                                        </a>
                                    </li>
                                    <li class="nav-item">
                                        <a class="nav-link nav-link-icon" href="#">
                                            <i class="ni ni-notification-70"></i>
                                            <span class="nav-link-inner--text d-lg-none">A venir</span>
                                        </a>
                                    </li>
                                </ul>
                            </base-nav>

</div>
                    <card type="secondary" shadow
                          header-classes="bg-white pb-5"
                          body-classes="px-lg-5 py-lg-5"
                          class="border-0 mb-3 ml-3 mr-3">
                         
<div class="emojis">

  <div class="emojiFly">
    😍
  </div>
  <div class="emojiFly">
    ❤️
  </div>
  <div class="emojiFly">
    👍
  </div>
  <div class="emojiFly">
    ❤️
  </div>
  <div class="emojiFly">
    👌
  </div>
  <div class="emojiFly">
    💯
  </div>
  <div class="emojiFly">
    👍
  </div>
  <div class="emojiFly">
    👌
  </div>

</div>



                        <template>
                            <div class="text-center text-muted mb-4">
                                <h2 class="policeH2"> {{uneCard.title}}</h2>
                            </div>
                                                        <div class="text-center text-muted mb-4">
                                <h3>Thématique:{{uneCard.sector}}</h3>
                            </div>
                            <div class="row"><p><strong>Auteur:</strong> {{uneCard.pseudo}}  <strong>Réseau:</strong>{{uneCard.socialNetwork}}</p></div>
                            
                          
                            <div class="row"><p>Estimation de réussite si la to do list est suivie: <span v-bind:class="[(uneCard.rateSucceed>50)?'text-success':'text-warning']" >{{uneCard.rateSucceed}} %</span></p></div>
                       <hr class="m-0">
<div class="row"><p>Description: {{uneCard.description}}</p></div>
<hr class="m-0">
<div class="row"><p><i class="ni ni-favourite-28"></i> {{nbrDeLikes}} | Abonnés: {{nbrDeUsers}}</p></div>
<hr class="m-0">
<div class="row"> 
    <div class="col-12">
      
        <button
          type="button"
          class="btn btn-primary btn-sm m-3"
          @click="likerMessage(uneCard._id, 1)"
          v-if="!(uneCard.usersLiked.includes(utilisateur))"
        >
          <i class="ni ni-favourite-28"></i> liker
        </button>
        <button
          type="button"
          class="btn btn-secondary btn-sm m-3"
          @click="likerMessage(uneCard._id, 0)"
          v-else
        >
          <i class="fa-solid fa-thumbs-up"></i> unliker
        </button>
      
    </div>  
 
            
                            
                            </div>
                       

<div class="row">
    <div class="col-sm-4 m-2"><button type="button" class="btn btn-danger btn-sm m-1"  v-if="utilisateur == uneCard.userId" @click="deleteCard(uneCard._id)">Supprimer</button></div>
</div>
    <div class="row">
    <div class="col-sm-4 m-2"><button type="button" class="btn btn-primary btn-sm m-1" @click="modals.modal1 = true">Partager</button></div>
    <div class="col-sm-4 m-2"><button type="button" class="btn btn-primary btn-sm m-1" @click.prevent="SeeOneCard(uneCard)">Voir</button></div>

</div>


            <modal :show.sync="modals.modal1">
                <h6 slot="header" class="modal-title" id="modal-title-default">Partager sur les réseau</h6>
                <p>Partagez avec vos amis cette ToDoList</p>
                <ul class="list-group">
<li class="list-group-item"><a target="_blank" :href="'https://www.facebook.com/sharer.php?u=https%3A%2F%2Fkribean.github.io%2Fachieve%2F%23%2FseeCardNtw%3Fid%3D'+this.uneCard._id">Share on Facebook</a></li>
<li class="list-group-item"><a target="_blank" :href="'https://twitter.com/intent/tweet?url=https%3A%2F%2Fkribean.github.io%2Fachieve%2F%23%2FseeCardNtw%3Fid%3D'+this.uneCard._id+'&text=Besoin%20d%20un%20coup%20de%20pouce%20pour%20vous%20aider%20%C3%A0%20d%C3%A9marrer%20vos%20projets!%20Suivez%20mon%20programme%20sur%20Achieve'">Share on Twitter</a></li>
<li class="list-group-item"><a target="_blank" :href="'https://www.linkedin.com/shareArticle?mini=true&url=https%3A//kribean.github.io/achieve/%23/seeCardNtw?id='+this.uneCard._id+'&title=Achieve&summary=Besoin%20d%20un%20coup%20de%20pouce%20pour%20vous%20aider%20%C3%A0%20d%C3%A9marrer%20vos%20projets!%20Suivez%20mon%20programme%20sur%20Achieve'">Share on LinkedIn</a></li>
                </ul>


                <template slot="footer">
                    <base-button type="link" class="ml-auto" @click="modals.modal1 = false">Close
                    </base-button>
                </template>
            </modal>


 
                        </template>
                    </card>
                    
                </div>
</template>
<script>
import BaseNav from "@/components/BaseNav";
import Modal from "@/components/Modal.vue";
export default {
    name: "CardPerso",
     components: {
    BaseNav,Modal
  },
   props: ["uneCard"],
   data() {
    return {
              modals: {
        modal1: false,
      },
      nbrDeLikes:this.uneCard.usersLiked.length+1,
      nbrDeUsers:this.uneCard.usersCard.length+1,
      utilisateur:JSON.parse(localStorage.leTokenUserAchieve).userId,
        
    }
},
methods: {
    SeeOneCard(uneCard){
        this.$router.push({ path: 'seeCard', query: { id: this.uneCard._id } });
    },
        //fonciton permettant de liker un message
    likerMessage(identiteMessage, valueLike) {

let divEmoji = document.querySelectorAll('.emojiFly');
for(const i of divEmoji)
{
    i.className='emoji';
}

      let url =
        "https://achieve-back.herokuapp.com/card/" + identiteMessage + "/like";
      fetch(url, {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          Authorization: "Bearer " + JSON.parse(localStorage.leTokenUserAchieve).token,
        },
        body: JSON.stringify({
          like: valueLike,
        }),
      }).then(() => {
          setTimeout(()=>{

              location.reload()
          },2500)
          ;
        });
      ;
    },
        //suppression du message si l'utilisateur a le droit
    deleteCard(id) {
      //on vérifie si le message contient une image ou non
        fetch(
          "https://achieve-back.herokuapp.com/card/" +
            id +
            "/deleteCard",
          {
            method: "DELETE",
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json",
              Authorization:
                "Bearer " + JSON.parse(localStorage.leTokenUserAchieve).token,
            },
          }
        ).then(() => {
          location.reload();
        });
     
    },

},

}

</script>
<style>
.policeH2{
    font-family: 'Luckiest Guy', cursive;
}

.emojiFly{
    display: none;
}

.emoji {
  color: #fff;
  font-size: 30px;
  font-family: Arial;
  text-shadow: 0 0 5px #000;
}

/*delete for no hover-effect*/
.emoji:hover {
  font-size: 60px;
  text-shadow: 5px 5px 5px white;
}

@-webkit-keyframes emojis-fall {
  0% {
    top: -10%;
  }
  100% {
    top: 100%;
  }
}
@-webkit-keyframes emojis-shake {
  0% {
    -webkit-transform: translateX(0px);
    transform: translateX(0px);
    opacity:1;
  }
  50% {
    -webkit-transform: translateX(20px);
    transform: translateX(20px);
  }
  100% {
    -webkit-transform: translateX(0px);
    transform: translateX(0px);
    opacity:0;
  }
}
@keyframes emojis-fall {
  0% {
    top: -10%;
  }
  100% {
    top: 100%;
  }
}
@keyframes emojis-shake {
  0% {
    transform: translateX(0px);
    opacity:1;
  }
  25% {
    transform: translateX(15px);
  }
  50% {
    transform: translateX(-15px);
  }
  100% {
    transform: translateX(0px);
    opacity:0;
  }
}

 .emoji  {
  position: fixed;
  top: -10%;
  opacity:0;
  z-index: 99999;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: default;
  -webkit-animation-name: emojis-fall, emojis-shake;
  -webkit-animation-duration: 2.5s, 1.5s;
  -webkit-animation-timing-function: linear, ease-in-out;

  -webkit-animation-play-state: running, running;
  animation-name: emojis-fall, emojis-shake;
  animation-duration: 2.5s, 1.5s;
  animation-timing-function: linear, ease-in-out;
  
  animation-play-state: running, running;
}
.emoji:nth-of-type(0) {
  left: 1%;
  -webkit-animation-delay: 0s, 0s;
  animation-delay: 0s, 0s;
}
.emoji:nth-of-type(1) {
  left: 10%;
  -webkit-animation-delay: 0.5s, 0.5s;
  animation-delay: 0.5s, 0.5s;
}
.emoji:nth-of-type(2) {
  left: 20%;
  -webkit-animation-delay: 3s, 0.25s;
  animation-delay: 3s, 0.25s;
}
.emoji:nth-of-type(3) {
  left: 30%;
  -webkit-animation-delay: 2s, 1s;
  animation-delay: 2s, 1s;
}
.emoji:nth-of-type(4) {
  left: 40%;
  -webkit-animation-delay: 1s, 1s;
  animation-delay: 1s, 1s;
}
.emoji:nth-of-type(5) {
  left: 50%;
  -webkit-animation-delay: 4s, 1.5s;
  animation-delay: 4s, 1.5s;
}
.emoji:nth-of-type(6) {
  left: 60%;
  -webkit-animation-delay: 3s, 1s;
  animation-delay: 3s, 1s;
}
.emoji:nth-of-type(7) {
  left: 70%;
  -webkit-animation-delay: 1.25s, 0.5s;
  animation-delay: 1.25s, 0.5s;
}
.emoji:nth-of-type(8) {
  left: 80%;
  -webkit-animation-delay: 0.5s, 0s;
  animation-delay: 0.5s, 0s;
}
.emoji:nth-of-type(9) {
  left: 90%;
  -webkit-animation-delay: 1.5s, 0.75s;
  animation-delay: 1.5s, 0.75s;
}

</style>