<template>
    <section class="section section-shaped section-lg my-0">
        <div class="shape shape-style-1 bg-gradient-default">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
        </div>
        <div class="container pt-lg-md text-white">
                      <base-alert type="info" icon="ni ni-bell-55" dismissible>
                <span slot="text"><strong>Info!</strong> Cliquez bien sur le bouton rejoindre pour utiliser une To Do List</span>
            </base-alert>
          <bar-perso></bar-perso>
          
            <div class="row justify-content-center">
<p>
    Rejoins une communauté de plus d'une centaine d'Achiever qui te conseillerons sur leurs succès et leurs échecs via leur ToDoList
</p>  
            </div>
                            <div class="row">
                                  
<div class="col-sm-3"><a class='btn btn-primary text-white  m-2' target="_blank" :href="'https://www.facebook.com/sharer.php?u=https%3A%2F%2Fkribean.github.io%2Fachieve%2F%23%2FseeCardNtw%3Fid%3D'+tabCard._id">Partager sur Facebook</a></div>
<div class="col-sm-3"><a class='btn btn-warning text-white  m-2' target="_blank" :href="'https://twitter.com/intent/tweet?url=https%3A%2F%2Fkribean.github.io%2Fachieve%2F%23%2FseeCardNtw%3Fid%3D'+tabCard._id+'&text=Besoin%20d%20un%20coup%20de%20pouce%20pour%20vous%20aider%20%C3%A0%20d%C3%A9marrer%20vos%20projets!%20Suivez%20mon%20programme%20sur%20Achieve'">Partager sur Twitter</a></div>
<div class="col-sm-3"><a class='btn btn-danger text-white  m-2' target="_blank" :href="'https://www.linkedin.com/shareArticle?mini=true&url=https%3A//kribean.github.io/achieve/%23/seeCardNtw?id='+tabCard._id+'&title=Achieve&summary=Besoin%20d%20un%20coup%20de%20pouce%20pour%20vous%20aider%20%C3%A0%20d%C3%A9marrer%20vos%20projets!%20Suivez%20mon%20programme%20sur%20Achieve'">Partager sur LinkedIn</a></div>
                
                </div>
                <div class="row justify-content-center"><h1 id="fontLucky" class=" text-white " >Mode d'emploi pour {{tabCard.title}}</h1></div>

                <div class="row">
                    <div class="col-6">
                        <h2 class="text-white">{{tabCard.sector}}</h2>
                    </div>
                     <div class="col-6">
                        <p class="text-white">Taux de réussite ressenti {{tabCard.rateSucceed}} %</p>
                    </div>
                </div>
                <div class="row">
                   <p class="text-white">Description: {{tabCard.description}}</p>
                </div>
                
                                    <div class="row" v-for="(i,index) in tabTasks" :key="index">
                <!--<base-alert type="success" icon="ni ni-like-2" dismissible>
                <span slot="text"><strong>Success!</strong> Bien ajouté</span>
            </base-alert>-->
            <div  class="col-10 bg-success m-2" v-if="i.isToDo==true">
            
<div class="row" v-if="boolDisabledOption==false">
    <div class="col-sm-8 col-md-8">
                        <p class="text-white">{{i.description}}</p>
                
    </div>
    <div class="col-lg-2 col-md-8">
                            <base-checkbox class="m-3" v-model="checkboxes.uncheckedDisabled" disabled>
                       J'ai réalisé cette tâche
                    </base-checkbox>
    </div>
        <div class="col-lg-2 col-md-8">
                            <base-checkbox class="m-3" v-model="checkboxes.uncheckedDisabled" disabled>
                        Je trouve cette tâche pertinente
                    </base-checkbox>
    </div>
</div>
<div class="row" v-else>
    <div class="col-sm-8 col-md-8">
                        <p class="text-white">{{i.description}}</p>
                
    </div>
    <div class="col-lg-2 col-md-8">
                           Ai-je réalisé cette tâche? <a class="btn btn-light btn-sm" @click="realisedFct(i._id)"><input v-if="i.realised.includes(user)" type="checkbox" checked><input v-else type="checkbox" ></a>
    </div>
        <div class="col-lg-2 col-md-8">
                            Est-ce pertinent?<a class="btn btn-light btn-sm" @click="pertinentFct(i._id)"><input v-if="i.relevant.includes(user)" type="checkbox" checked><input v-else type="checkbox" ></a>
    </div>
</div>



            </div>

                        <div  class="col-10 bg-danger m-2" v-if="i.isToDo==false">
            
<div class="row">
    <div class="col-8">
                        <p class="text-white">{{i.description}}</p>
                
    </div>
    <div class="col-2">
                            <base-checkbox class="mb-3" v-model="checkboxes.checkedRealisedTask" disabled>
                        Unchecked
                    </base-checkbox>
    </div>
        <div class="col-2">
                            <base-checkbox class="mb-3" v-model="checkboxes.checkedPertinent" disabled>
                        Unchecked
                    </base-checkbox>
    </div>
</div>
            </div>
                </div>
              
                <div class="row">
                    <p>Vous souhaitez utiliser cette ToDoList pour votre avancée? Cliquez sur Rejoindre. Si vous n'êtes pas encore <strong>inscrit</strong> cliquez sur rejoindre et inscrivez vous gratuitement!</p>
                    <button v-if="!(boolDisabledOption)" class="btn btn-light btn-sm m-2" @click.prevent="rejoindre">Rejoindre</button>
                </div>

        </div>
                 

    </section>

</template>
<script>
import Modal from "@/components/Modal.vue";
import BaseSlider from "../components/BaseSlider.vue";
import BaseDropdown from "../components/BaseDropdown.vue";
import BaseAlert from "../components/BaseAlert.vue";
import BaseInput from "../components/BaseInput.vue";
import Tabs from "@/components/Tabs/Tabs.vue";
import TabPane from "@/components/Tabs/TabPane.vue";
import BaseNav from "@/components/BaseNav";
import CardPerso from "./components/JavascriptComponents/CardPerso.vue";
import BarPerso from "./components/JavascriptComponents/BarPerso.vue";
import BaseCheckbox from "../components/BaseCheckbox.vue";

export default {
     components: {
    Modal,BaseSlider,BaseDropdown,BaseInput,Tabs,TabPane,BaseNav,CardPerso,BaseCheckbox,BarPerso,BaseAlert
  },
 
  data() {
    return {
            modals: {
        modal1: false,
      },
        tabCard:{},
        tabTasks:[],
      boolDisabledOption:false,
      user:JSON.parse(localStorage.leTokenUserAchieve).userId,
      checkboxes:{
        checkedRealisedTask:false}
    };
  },
  methods: {
      rejoindre(){
          if(localStorage.leTokenUserAchieve && !(this.tabCard.usersCard.includes(JSON.parse(localStorage.leTokenUserAchieve).userId)))
      {
          fetch("https://achieve-back.herokuapp.com/card/"+this.$route.query.id +"/modifyCardAny", {
          method: "PUT",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
            Authorization:
              "Bearer " + JSON.parse(localStorage.leTokenUserAchieve).token,
          },
          body: JSON.stringify({
            usersCardId: JSON.parse(localStorage.leTokenUserAchieve).userId,

          }),
        })
        .then((response) => {
          location.reload()})
        .catch(()=>{console.log('petit blem')})
      }
      else{console.log('inscrivez vous');
      this.router.push('register')
      }
      },

      realisedFct(idTask){   
          fetch("https://achieve-back.herokuapp.com/card/tasks/"+idTask +"/realisedTask", {
          method: "PUT",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
            Authorization:
              "Bearer " + JSON.parse(localStorage.leTokenUserAchieve).token,
          },
          body: JSON.stringify({
            usersCardId: JSON.parse(localStorage.leTokenUserAchieve).userId,

          }),
        })
        .then(() => location.reload())
        .catch(()=>{console.log('blem')})
      },


      pertinentFct(idTask){
          fetch("https://achieve-back.herokuapp.com/card/tasks/"+idTask +"/relevantTask", {
          method: "PUT",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
            Authorization:
              "Bearer " + JSON.parse(localStorage.leTokenUserAchieve).token,
          },
          body: JSON.stringify({
            usersCardId: JSON.parse(localStorage.leTokenUserAchieve).userId,

          }),
        })
        .then(() => location.reload())
        .catch(()=>{console.log('blem')})
      }

      
  },
    created() {
        let idUrl=this.$route.query.id
    fetch("https://achieve-back.herokuapp.com/card/"+idUrl, {
      method: "GET",
      headers: {
        'Accept': "application/json",
        "Content-Type": "application/json",
       
      },
    })
      .then((result) => {
        return result.json();
      })
      .then((tableauCard) => {
        this.tabCard = tableauCard;
        this.boolDisabledOption=this.tabCard.usersCard.includes(JSON.parse(localStorage.leTokenUserAchieve).userId);
       
      return     fetch("https://achieve-back.herokuapp.com/card/"+idUrl+"/allTasks", {
      method: "GET",
      headers: {
        'Accept': "application/json",
        "Content-Type": "application/json",
       
      },
    });})
    .then((result)=>{
return result.json();
    })
    .then((tabTasks)=>{
this.tabTasks=tabTasks;
    });
  }, 
};
</script>
<style >
#fontLucky{
    font-family: 'Luckiest Guy', cursive;
}
</style>
