<template>
        <div class="row">
          <div class="col-12">
                                <router-link  to="/room" class="btn btn-primary m-2">Home</router-link>
                    <router-link to="/profile" class="btn btn-primary  m-2">Profile</router-link>
                    <button class="btn btn-danger m-2" @click="disconnect">Se déconnecter</button>

          </div>
        </div>
</template>
<script>
export default {
    methods: {
              disconnect(){
            //lors de la déconnexion, les informations stocké par rapport au token sont supprimé et l'utilisateur est envoyé sur la page login
      localStorage.removeItem("leTokenUserAchieve");
      this.$router.push("login");
  }
    },

}
