        <section class="section section-skew">
            <div class="container">
                <card shadow class="card-profile mt--300" no-body>
                    <div class="px-4">
                        <div class="row justify-content-center">
                            <div class="col-lg-3 order-lg-2">
                                <div class="card-profile-image">
                                    <a href="#">
                                        <img v-lazy="'img/theme/team-4-800x800.jpg'" class="rounded-circle">
                                    </a>
                                </div>
                            </div>
                            <div class="col-lg-4 order-lg-3 text-lg-right align-self-lg-center">
                                <div class="card-profile-actions py-4 mt-lg-0">
        <button type="submit" @click="deleteProfil" class="btn btn-danger m-3">
          Supprimer mon compte
        </button>
                                </div>
                            </div>
                            <bar-perso></bar-perso>
                            <div class="col-lg-4 order-lg-1">
                                <div class="card-profile-stats d-flex justify-content-center">
                                    <div>
                                        <span class="heading">220</span>
                                        <span class="description">Utilisateurs</span>
                                    </div>
                                    <div>
                                        <span class="heading">11/04/2022</span>
                                        <span class="description">Existence de Achieve</span>
                                    </div>
                                    <div>
                                        <span class="heading">Créateur</span>
                                        <span class="description">Bruno RAMAEL</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="text-center mt-5">
    <form class="formulaire">
      <div class="form-group">
        <label for="pseudo">Pseudo</label>
        <input
          type="text"
          class="form-control"
          id="pseudo"
          v-model="pseudo"
          aria-describedby="pseudonyme"
          placeholder="Entrer pseudo"
          pattern="[A-Za-z]{2,10}[ -]?[A-Za-z]{2,10}"
          required
        />
        <p id="pseudoErrorMsg"></p>
      </div>
            <div class="form-group">
        <label for="reseau">Réseaux sociaux</label>
        <input
          type="text"
          class="form-control"
          id="reseau"
          v-model="reseau"
          aria-describedby="réseaux sociaux"
          placeholder="Entrer l'adresse de vos réseaux sociaux"

        />
        <p id="reseauErrorMsg"></p>
      </div>
      <button type="submit" @click="verifyUserData" class="btn btn-primary m-3">
        Modifier
      </button>
    </form>
                        </div>
                                                <div class="text-center mt-5">

                          <p> Mes To Do Lists suivies</p>
                          <div class="row justify-content-center bg-success m-2" v-for="(card,index) in tabCardsFromUserFollowed" :key="index" >
                          <div class="col-9">
<p class="text-white">n°{{index}} Titre: {{card.title}}</p>
