    </div>
</template>
<script>
import BarPerso from "./components/JavascriptComponents/BarPerso.vue";
import BaseButton from "../components/BaseButton.vue";
export default {
     components: {
    BarPerso,
    BaseButton
},
     data() {
    return {

      pseudo: JSON.parse(localStorage.leTokenUserAchieve).pseudo,
      reseau: JSON.parse(localStorage.leTokenUserAchieve).socialNetwork,
      tabCardsFromUser:[],
      tabCardsFromUserFollowed:[],
    };
  },
  methods: {
    //vérification des champs utilisateurs
    verifyUserData() {
      let lesValiditeDeChamps = true;
      for (let input of document.querySelectorAll(".formulaire input")) {
        //on ne prend pas en compte le bouton submit
        if (input.getAttribute("type") != "submit") {
          //on verifie l'identité des champs, autre que le bouton submit si un des champs n'est pas valide on met le booléen lesValiditeDeChamps à false
          if (!input.reportValidity()) {
            lesValiditeDeChamps = false;
            document.getElementById(
              input.getAttribute("id").concat("ErrorMsg")
            ).textContent = "Vous avez mal complété ce champ";
          } else {
            document.getElementById(
              input.getAttribute("id").concat("ErrorMsg")
            ).textContent = "";
          }
        }
      }
      //si les champ sont valides on envoie au back pour traitement
      if (lesValiditeDeChamps) {
        fetch("https://achieve-back.herokuapp.com/user/modifyProfil", {
          method: "PUT",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
            Authorization:
              "Bearer " + JSON.parse(localStorage.leTokenUserAchieve).token,
          },
          body: JSON.stringify({
            pseudo: this.pseudo,
            socialNetwork:this.reseau
          }),
        }).then(() => {
          //modification des champs dans le local storage
          localStorage.setItem(
            "leTokenUserAchieve",
            JSON.stringify({
              pseudo: this.pseudo,
              socialNetwork:this.reseau,
              userId: JSON.parse(localStorage.leTokenUserAchieve).userId,
              token: JSON.parse(localStorage.leTokenUserAchieve).token,
            })
          );
          this.$router.push("room");
        });
      }
    },
    //suppression du profil et des posts liés au profil
    deleteProfil() {
      fetch("https://achieve-back.herokuapp.com/user/deleteProfil", {
        method: "DELETE",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          Authorization: "Bearer " + JSON.parse(localStorage.leTokenUserAchieve).token,
        },
      }).then(() => {
        localStorage.removeItem("leTokenUserAchieve");
        this.$router.push("login");
      });
    },
 
  },
      created() {
    
    fetch("https://achieve-back.herokuapp.com/card/"+JSON.parse(localStorage.leTokenUserAchieve).userId+"/cardsUser", {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.leTokenUserAchieve).token,
      },
    })
      .then((result) => {
       
        return result.json();
      })
      .then((tableauCards) => {
        ;
        this.tabCardsFromUser = tableauCards;
      });

    fetch("https://achieve-back.herokuapp.com/card/", {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.leTokenUserAchieve).token,
      },
    })
      .then((result) => {
       
        return result.json();
      })
      .then((tableauCards) => {
       
        this.tabCardsFromUserFollowed=tableauCards.filter(element => element.usersCard.includes(JSON.parse(localStorage.leTokenUserAchieve).userId));
        
      });

  },

};
</script>
